/* 登录请求API */

import request from '@/utils/request'

/* 请求图形验证码图片 */
export const getPictureCode = () => {
  return request.get('/captcha/image')
}

/* 发送短信验证码 */
export const postSmsCode = (captchaCode, captchaKey, mobile) => {
  return request.post('/captcha/sendSmsCaptcha', {
    form: {
      captchaCode, // 图形验证码
      captchaKey, // 图形验证码key
      mobile // 手机号
    }
  })
}

/* 验证码登录 */
export const smsCodeLogin = (mobile, smsCode) => {
  return request.post('/passport/login', {
    form: {
      isParty: false,
      mobile,
      partyData: {},
      smsCode
    }
  })
}
