<template>
  <div class="login">
    <!-- 顶栏 -->
    <van-nav-bar title="会员登录" left-arrow @click-left='$router.go(-1)' ></van-nav-bar>

    <!-- 内容 -->
    <div class="container">

      <!-- 标题 -->
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后自动注册</p>
      </div>

      <!-- 表单 -->
      <div class='form'>
        <div class="form-item">
          <input v-model="mobileNum" class='inp' maxlength="11" placeholder='请输入手机号' type="text">
        </div>
        <div class="form-item">
          <input  v-model='picCode' class='inp' maxlength='5' placeholder='请输入图形验证码' type="text">
          <img v-if='picUrl' :src='picUrl' @click='getPicCode' alt = ''>
        </div>
        <div class="form-item">
          <input v-model="smsCode" class="inp" placeholder="请输入短信验证码 默认: 246810" type="text">
          <button @click="GetSmsCode">
            {{ timer === null ? '获取验证码' : currentSecond + '秒后重新获取' }}
          </button>
        </div>
      </div>

      <!-- 按钮 -->
       <div @click='verifyLogin' class="login-btn">登录</div>
    </div>
  </div>
</template>

<script>

/* 按需导入login的API下的对应函数 */
import { getPictureCode, postSmsCode, smsCodeLogin } from '@/api/login.js'
import { addAddress } from '@/api/address'
export default {
  name: 'LoginIndex',
  data () {
    return {
      picCode: '', // 用户输入的图形验证码
      picKey: '', // 图形验证码唯一标识key
      picUrl: '', // 图片渲染地址base64
      totalSecond: 5, // 验证码总秒数
      currentSecond: 5, // 验证码现在的秒数
      timer: null, // 计时器id
      mobileNum: '', // 手机号
      smsCode: '' // 手机验证码
    }
  },

  created () {
    this.getPicCode()
  },

  methods: {

    /* 获取图形验证码 */
    async getPicCode () {
      const { data: { base64, key } } = await getPictureCode()
      this.picUrl = base64 // 图片地址
      this.picKey = key // 图片唯一标识
    },

    /* 手机号和图形验证码验证 */
    verifyCode () {
      const phoneNumReg = /^1[3-9]\d{9}$/ // 手机号正则
      const picCodeReg = /^\w{4}$/ // 图形验证码正则
      if (!phoneNumReg.test(this.mobileNum)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!picCodeReg.test(this.picCode)) {
        this.$toast('图形验证码错误')
        return false
      }
      return true
    },

    /* 获取短信验证码 */
    async GetSmsCode () {
      if (!this.verifyCode()) {
        return
      }
      // 节流,避免重复点击
      if (!this.timer && this.totalSecond === this.currentSecond) {
        // 发送验证码请求
        await postSmsCode(this.picCode, this.picKey, this.mobileNum)
        // 开启计时器
        this.timer = setInterval(() => {
          this.currentSecond--
          // 倒计时归零
          if (this.currentSecond <= 0) {
            clearInterval(this.timer)
            this.timer = null
            this.currentSecond = this.totalSecond
          }
        }, 1000)
        this.$toast('发送成功')
      }
    },

    /* 验证码登录 */
    async verifyLogin () {
      if (!this.verifyCode) {
        return
      }
      if (!/^\d{6}$/.test(this.smsCode)) {
        this.$toast('手机验证码错误')
        return
      }
      // 发送登录请求
      const res = await smsCodeLogin(this.mobileNum, this.smsCode)
      // 提交用户权限信息
      this.$store.commit('user/setUserInfo', res.data)
      // 添加默认地址
      await addAddress(
        '张三',
        this.mobileNum,
        [
          {
            value: 782,
            label: '上海'
          },
          {
            value: 783,
            label: '上海市'
          },
          {
            value: 785,
            label: '徐汇区'
          }
        ],
        '北京路1号楼8888室')
      // 返回首页
      this.$toast('登录成功')
      // 是否有跳跃返回拦截地址
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    }
  },

  destroyed () {
    // 离开页面清空定时器
    clearInterval(this.timer)
    this.timer = null
  }

}
</script>

<style lang='less' scoped>
.container{
  padding: 49px 29px;

  .title{
    margin-bottom: 20px;
    h3{
      font-size:26px;
      font-weight: normal;
    }
    p{
      line-height: 40px;
      font-size:14px;
      color:#b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
